<template>
  <div class="system-authorization">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      ref="system_authorization_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="onLineRegister">在线授权注册</el-button>
        <el-button type="primary" size="mini" @click="offLineRegister">离线授权注册</el-button>
      </template>
      <!-- 产品名称 -->
      <template v-slot:product_name_head="props">
        <el-input
          v-model="params.product_name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 适配平台版本号 -->
      <template v-slot:product_m_version_head="props"></template>
      <!-- 模块版本号 -->
      <template v-slot:product_c_version_head="props"></template>
      <!-- 产品数量 -->
      <template v-slot:number_head="props"></template>
      <!-- 开始时间 -->
      <template v-slot:stime_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          v-model="params.stime"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
      <!-- 终止时间 -->
      <template v-slot:etime_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          v-model="params.etime"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import onlineRegister from "./online_register";
import offlineRegister from "./offline_register";

export default {
  data() {
    return {
      url: "/auth/list",
      type: "get",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "模块名称",
          sortable: true,
          prop: "product_name",
          second_head: {
            minWidth: "200px",
            prop: "product_name",
            templateHeadName: "product_name_head"
          }
        },
        {
          label: "适配平台版本号",
          sortable: true,
          prop: "product_m_version",
          second_head: {
            minWidth: "100px",
            prop: "product_m_version",
            templateHeadName: "product_m_version_head"
          }
        },
        {
          label: "模块版本号",
          sortable: true,
          prop: "product_c_version",
          second_head: {
            minWidth: "100px",
            prop: "product_c_version",
            templateHeadName: "product_c_version_head"
          }
        },
        {
          label: "产品数量",
          prop: "number",
          sortable: true,
          second_head: {
            minWidth: "100px",
            prop: "number",
            templateHeadName: "number_head"
          }
        },
        {
          label: "开始时间",
          prop: "stime",
          sortable: true,
          second_head: {
            minWidth: "200px",
            templateHeadName: "stime_head",
            prop: "stime"
          }
        },
        {
          label: "终止时间",
          prop: "etime",
          sortable: true,
          second_head: {
            minWidth: "200px",
            templateHeadName: "etime_head",
            prop: "etime"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        product_m_version: "",
        product_name: "",
        number: "",
        etime: "",
        stime: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      }
    };
  },
  methods: {
    reload() {
      this.$refs.system_authorization_table.reload();
    },
    search() {
      this.params.page = 1;
      this.reload();
    },
    onLineRegister() {
      this.$layer.iframe({
        content: {
          content: onlineRegister,
          parent: this,
          data: {}
        },
        area: ["500px", "auto"],
        title: "产品在线注册",
        shadeClose: false
      });
    },
    offLineRegister() {
      this.$layer.iframe({
        content: {
          content: offlineRegister,
          parent: this,
          data: {}
        },
        area: ["500px", "200px"],
        title: "产品离线注册",
        shadeClose: false
      });
    }
  }
};
</script>


