import { ajaxPro } from "req/http/http";

export function getInformation() {
  return ajaxPro({
    url: "/auth/list",
    type: "get"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

export function onlineRegister(param) {
  return ajaxPro({
    url: "/auth/online",
    data: param,
    type: "post"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

export function offlineRegister(param) {
  return ajaxPro({
    url: "/auth/offline",
    data: param,
    contentType: false,
    processData: false,
    type: "post"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}



