<template>
  <!-- <el-card class="system-authorization-online_register"> -->
    <!-- <div slot="header" class="clearfix">
      <span>产品注册(在线)</span>
    </div> -->

    <div class="authorization-online_register-wrap">
      <mu-form
        :model="formData"
        class="mu-demo-form"
        :label-position="labelPosition"
        label-width="60"
        ref="authorization_online_register"
      >
        <!-- <mu-form-item prop="usm" label="用户名" :rules="usmRules">
          <mu-text-field v-model="formData.usm" :max-length="50" placeholder="请输入用户名"></mu-text-field>
        </mu-form-item> -->
        <mu-form-item prop="key" label="序列号" :rules="keyRules">
          <mu-text-field v-model="formData.key" :max-length="50" placeholder="请输入序列号"></mu-text-field>
        </mu-form-item>
        <mu-form-item>
          <mu-button color="primary" small @click="submit">确定</mu-button>
          <mu-button small @click="clear">取消</mu-button>
        </mu-form-item>
      </mu-form>
    </div>
  <!-- </el-card> -->
</template>
<script>
import {onlineRegister} from 'req/api/system/authorization'

export default {
  name: "system_authorization_online_register",
  data() {
    return {
      labelPosition: "right",
      // usmRules: [
      //   {
      //     validate: val => !!val,
      //     message: "用户名不能为空"
      //   }
      // ],
      keyRules: [{ validate: val => !!val, message: "序列号不能为空" }],
      formData: {
        // usm: "",
        key: ""
      }
    };
  },
  props: {
    layerid: {
      type: String
    }
  },
  methods: {
    submit() {
      this.$refs.authorization_online_register.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_authorization",
            "确定提交吗？",
            () => {
              this.__onlineRegister(this.formData);
            },
            this
          );
        }
      });
    },
    clear() {
      this.$isShowTip(
        "system",
        "system_authorization",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid)
          /* this.$refs.authorization_online_register.clear();
          this.formData = {
            usm: "",
            key: ""
          }; */
        },
        this
      );
    },
    __onlineRegister(param) {
      onlineRegister(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "注册成功"
          });
          this.$layer.close(this.layerid)
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less" scoped>
.authorization-online_register-wrap {
  width: 100%;
  padding:20px 20px 0 20px ;
}
</style>


