<template>
  <el-card class="system-authorization-offline_register">
    <!-- <div slot="header" class="clearfix">
      <span>产品注册(离线)</span>
    </div> -->
    <el-divider content-position="left">请选择要上传的文件</el-divider>
    <div class="authorization-offline_register-wrap">
      <input type="file" ref="upload" accept=".key" class="offline_register-input" @change="fileChange" />
      <el-button size="small" type="primary" @click="selectFile">选取文件</el-button>
      <el-button
        style="margin-left: 10px;"
        size="small"
        type="success"
        v-show="fileList.length > 0 && !fileSizeExceed"
        @click="submitUpload"
      >上传到服务器</el-button>
      <div class="selectFileSuccess" v-show="fileList.length > 0 && !fileSizeExceed">
        <i class="el-icon-document"></i>
        {{fileName}}
        <i class="el-icon-check icon-success-style"></i>
      </div>
      <div class="selectFileError" v-if="fileSizeExceed">
        <i class="el-icon-info icon-error-style"></i>上传文件不能大于512M
      </div>
    </div>
  </el-card>
</template>
<script>
import {offlineRegister} from 'req/api/system/authorization'

export default {
  name: "system_authorization_offline_register",
  data() {
    return {
      fileList: [],
      fileName: "",
      fileSize: "",
      fileSizeExceed: false
    };
  },
  created() {},
  methods: {
    selectFile() {
      this.$refs.upload.click();
    },
    fileChange() {
      let files = this.$refs.upload.files;
      this.fileSize = files[0].size;
      this.fileSizeExceed = this.fileSize > 1024 * 1024 * 512 ? true : false;
      this.fileList = files;
      this.fileName = files[0].name;
    },
    submitUpload() {
      let formData = new FormData;
      formData.append('file',this.fileList[0])
      let now_time = new Date().getTime() / 1000 | 0
      formData.append("now_time",now_time );
      this.__offlineRegister(formData)
    },
    __offlineRegister(param) {
      offlineRegister(param).then(res => {
        console.log(res)
        this.$message({
            type: "success",
            message: "上传成功"
          });
      }).catch(err => {})
    }
  }
};
</script>
<style lang="less" scoped>
.tipPadding() {
  padding:10px 0 0 0;
}
.authorization-offline_register-wrap {
  width: 50%;
  min-width: 500px;
  .offline_register-input {
    display: none;
  }
  .selectFileSuccess {
    .tipPadding();
    color:#666;
  }
  .selectFileError {
    .tipPadding();
    color:red;
  }
}
</style>



